<template>
  <div class="container" ref="box">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>试题录入</span>
      </div>
      <el-form ref="form" :model="form" :rules="rules" label-width="100px">
        <el-form-item label="学科：" prop="subjectID">
          <el-select
            v-model="form.subjectID"
            placeholder="请选择"
            @change="onChange(form.subjectID)"
          >
            <el-option
              v-for="(item, index) in subjectsList"
              :key="index"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="目录：" prop="catalogID">
          <el-select v-model="form.catalogID" placeholder="请选择">
            <el-option
              v-for="(item, index) in catalogList"
              :key="index"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="企业：" prop="enterpriseID">
          <el-select v-model="form.enterpriseID" placeholder="请选择">
            <el-option
              v-for="(item, index) in companysList"
              :key="index"
              :label="item.company"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="城市：" prop="province">
          <el-select
            v-model="form.province"
            placeholder="请选择"
            style="width:195px"
            @change="handleProvince"
          >
            <el-option
              v-for="item in citySelect.province"
              :key="item"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
          <el-select
            v-model="form.city"
            filterable
            placeholder="请选择"
            style="margin-left:10px;width:195px"
            @keyup.enter="handleFilter"
          >
            <el-option
              v-for="item in citySelect.cityDate"
              :key="item"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="方向：" prop="direction">
          <el-select v-model="form.direction" placeholder="请选择">
            <el-option
              v-for="(item, index) in directionList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="题型：" prop="questionType">
          <el-radio-group v-model="form.questionType" @change="change">
            <el-radio label="1">单选</el-radio>
            <el-radio label="2">多选</el-radio>
            <el-radio label="3">简答</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="难度：" prop="difficulty">
          <el-radio-group v-model="form.difficulty">
            <el-radio label="1">简单</el-radio>
            <el-radio label="2">一般</el-radio>
            <el-radio label="3">困难</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="题干：" prop="question">
          <quill-editor
            v-model="form.question"
            :options="editorOption"
            style="height:250px;margin-bottom:50px"
          ></quill-editor>
        </el-form-item>
        <el-form-item v-if="form.questionType !== '3'" label="选项:">
          <div
            v-for="(item, index) in options"
            :key="index"
            style="display:flex;align-items:center;padding-bottom:20px"
          >
            <el-radio
              v-if="form.questionType === '1'"
              v-model="radio"
              :label="item.code"
              :checked="item.isRight"
              style="margin:0"
              >{{ item.code }}:
            </el-radio>
            <el-checkbox
              v-else-if="form.questionType === '2'"
              v-model="item.isRight"
              >{{ item.code }}:
            </el-checkbox>
            <el-input v-model="item.title" style="width:240px;margin:0 5px">
            </el-input>
            <upload-img @getUrl="getUrl" :index="index"></upload-img>
          </div>
          <el-button
            :disabled="form.questionType === '1'"
            size="small"
            type="danger"
            @click="addItem"
            >+增加选项与答案
          </el-button>
        </el-form-item>
        <el-form-item label="解析视频：" style="width:400px">
          <el-input v-model="form.videoURL" type="text"></el-input>
        </el-form-item>
        <el-form-item label="答案解析：" prop="answer">
          <quill-editor
            ref="myTextEditor"
            v-model="form.answer"
            :options="editorOption"
            style="height:250px;margin-bottom:50px"
          ></quill-editor>
        </el-form-item>
        <el-form-item label="题目备注：" style="width:400px">
          <el-input v-model="form.remarks" type="textarea"></el-input>
        </el-form-item>

        <el-form-item label="试题标签：">
          <el-select
            v-model="form.tags"
            multiple
            filterable
            allow-create
            default-first-option
            placeholder="请选择文章标签"
          >
            <el-option
              v-for="(item, index) in tagList"
              :key="index"
              :label="item.label"
              :value="item.label"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="success" @click="onUpdate" v-if="$route.query.id"
            >确认修改</el-button
          >
          <el-button type="primary" @click="onSubmit" v-else
            >确认提交</el-button
          >
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { citys, provinces } from '@/api/hmmm/citys.js'
// 本地引入方向列表
import { direction } from '@/api/hmmm/constants.js'
import { simple } from '@/api/hmmm/subjects.js'
import { simple as simpleD } from '@/api/hmmm/directorys.js'
import { simple as simpleT } from '@/api/hmmm/tags.js'
import { list } from '@/api/hmmm/companys.js'
import { add, detail, update } from '@/api/hmmm/questions.js'

// 富文本编辑器
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
// 引入上传图片组件
import uploadImg from '@/module-hmmm/components/upload-img'

export default {
  // 注册组件
  data () {
    return {
      citySelect: {
        province: [],
        cityDate: []
      },
      // 添加选项按钮禁用
      disabled: false,
      radio: '',
      checked: false,
      // 方向列表
      directionList: direction,
      // 学科列表
      subjectsList: [],
      // 学科详情列表
      catalogList: [],
      // 学科对应标签列表
      tagList: [],
      // 框框里面有的数据
      // value: [],
      // 公司列表
      companysList: [],
      flag: true,
      sum: 0,
      // 富文本编辑框配置项
      editorOption: {
        modules: {
          toolbar: [
            [
              'bold',
              'italic',
              'underline',
              'strike',
              { list: 'ordered' },
              { list: 'bullet' },
              'blockquote',
              'code-block',
              'image',
              'link'
            ]
          ]
        }
      },
      // 选项配置项
      options: [
        {
          isRight: false,
          code: 'A',
          title: '',
          img: ''
        },
        {
          isRight: false,
          code: 'B',
          title: '',
          img: ''
        },
        {
          isRight: false,
          code: 'C',
          title: '',
          img: ''
        },
        {
          isRight: false,
          code: 'D',
          title: '',
          img: ''
        }
      ],
      // 准备一个装选项code索引的变量
      num: 0,
      // 表单数据绑定
      form: {
        number: '',
        subjectID: '',
        catalogID: '',
        enterpriseID: '',
        province: '',
        city: '',
        direction: '',
        questionType: '1',
        difficulty: '1',
        question: '',
        options: [],
        videoURL: '',
        answer: '',
        remarks: '',
        tags: []
      },
      // 表单验证规则
      rules: {
        subjectID: [
          {
            required: true,
            message: '请选择学科',
            trigger: 'change'
          }
        ],
        catalogID: [
          {
            required: true,
            message: '请选择目录',
            trigger: 'change'
          }
        ],
        enterpriseID: [
          {
            required: true,
            message: '请选择企业',
            trigger: 'change'
          }
        ],
        province: [
          {
            required: true,
            message: '请选择省份',
            trigger: 'change'
          }
        ],
        city: [
          {
            required: true,
            message: '请选择城市',
            trigger: 'change'
          }
        ],
        direction: [
          {
            required: true,
            message: '请选择方向',
            trigger: 'change'
          }
        ],
        questionType: [
          {
            required: true,
            message: '请选择题型',
            trigger: 'change'
          }
        ],
        difficulty: [
          {
            required: true,
            message: '请选择难度',
            trigger: 'change'
          }
        ],
        question: [
          {
            required: true,
            message: '请输入题干',
            trigger: 'onblur'
          }
        ],
        answer: [
          {
            required: true,
            message: '请选择活动区域',
            trigger: 'change'
          }
        ]
      }
    }
  },
  components: {
    quillEditor,
    uploadImg
  },
  watch: {
    // 监听单选框的变化
    radio (newVal) {
      this.options.forEach(item => {
        if (item.code === newVal) {
          item.isRight = true
        } else {
          item.isRight = false
        }
      })
    }
  },
  updated () {
    // 此时页面数据渲染了5次
    // console.log(11111)
    if (this.sum < 5 && this.$route.query.id) {
      this.$refs.box.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.scrollTop = 0
      this.sum++
      console.log(this.sum)
    }
  },
  // 创建完毕状态
  created () {
    this.getCityData()
    this.getSubjects()
    this.getCompanys()
    this.getDetail()
  },
  methods: {
    // 选项排序
    sortBy (attr, rev) {
      // 第二个参数没有传递 默认升序排列
      if (rev == undefined) {
        rev = 1
      } else {
        rev = rev ? 1 : -1
      }
      return function (a, b) {
        a = a[attr]
        b = b[attr]
        if (a < b) {
          return rev * -1
        }
        if (a > b) {
          return rev * 1
        }
        return 0
      }
    },
    // 获取 id 渲染页面
    async getDetail () {
      // console.log(this.$route.query.id)
      if (this.$route.query.id) {
        const { data } = await detail({ id: this.$route.query.id })
        console.log(data)
        // 给表单赋值
        this.form = data
        this.getCatalog()
        // 渲染选项列表
        if (Number(data.questionType) === 1) {
          data.options.forEach(item => {
            if (Number(item.isRight) === 1) {
              this.radio = item.code
            }
          })
        } else if (Number(data.questionType) === 2) {
          data.options.forEach(item => {
            if (Number(item.isRight) === 1) {
              item.isRight = true
            } else {
              item.isRight = false
            }
          })
        }
        // 修改之后把数据赋给表单绑定的数组
        this.options = data.options
        this.options.sort(this.sortBy('code', true))
        if (data.tags === '') {
          this.form.tags = []
        } else {
          this.form.tags = data.tags.split(',')
        }
        // this.form.tags = data.tags
        console.log(this.form.tags, 888999555)
      }
    },
    // 获取全部学科列表
    async getSubjects () {
      const res = await simple()
      console.log(res)
      if (res.status === 200) {
        this.subjectsList = res.data
        console.log(this.subjectsList)
      }
    },
    // 学科发生变化
    onChange (id) {
      console.log(id, 99999)
      // 先清空目录和tags
      this.form.catalogID = ''
      this.form.tags = []
      // 获取目录
      this.getCatalog(id)
      // 获取tags
      this.getTags(id)
    },
    // 根据学科id获取学科目录详情
    async getCatalog (id) {
      this.catalogList = []
      console.log(id)
      // 获取学科目录
      const res = await simpleD({
        subjectID: id
      })
      console.log(res, 99999)
      if (res.status === 200) {
        this.catalogList = res.data
        console.log(this.catalogList)
      }
    },
    // 根据学科id获取tags详情
    async getTags (id) {
      // 获取标签列表
      // 先清空原有的数据
      const res = await simpleT({
        subjectID: id
      })
      console.log(res, 99999)
      if (res.status === 200) {
        this.tagList = res.data
        console.log(this.tagList, 889977)
      }
    },
    // 获取全部公司列表
    async getCompanys () {
      const res = await list()
      console.log(res)
      if (res.status === 200) {
        this.companysList = res.data.items
        console.log(this.companysList)
      }
    },
    // 获取省
    getCityData: function () {
      this.citySelect.province = provinces()
    },
    // 选省获取到市
    handleProvince: function (e) {
      this.citySelect.cityDate = citys(e)
      this.form.city = this.citySelect.cityDate[0]
    },
    // 多选的时候动态添加选项
    addItem () {
      // 准备装选项code的数组
      var letterArr = []
      for (var i = 69, j = 0; i < 126; i++, j++) {
        letterArr[j] = String.fromCharCode(i)
      }
      this.options.push({
        isRight: false,
        code: letterArr[this.num],
        title: '',
        img: ''
      })
      this.num++
    },
    // 切换题型的时候让所有的isRight都变成false
    change () {
      this.options.forEach(item => {
        item.isRight = false
        this.radio = ''
      })
    },
    // 点击提交
    onSubmit () {
      this.form.tags = this.form.tags.join(',')
      this.form.options = this.options
      this.$refs.form.validate(async valid => {
        if (valid) {
          console.log(valid)
          console.log(11111)
          const res = await add(this.form)
          console.log(res)
          if (res.status === 200) {
            this.$message({
              message: '添加成功！',
              type: 'success'
            })
            this.$router.push('/questions/list')
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 点击修改
    onUpdate () {
      this.form.tags = this.form.tags.join(',')
      console.log(this.form.tags, 111)
      console.log(this.form.tags, 222)
      this.form.options = this.options
      // console.log(this.$refs.form, 778899)
      // console.log(this.form)
      this.$refs.form.validate(async valid => {
        if (valid) {
          console.log(valid)
          console.log(11111)
          const res = await update(this.form)
          console.log(res)
          if (res.status === 200) {
            this.$message({
              message: '修改成功！',
              type: 'success'
            })
            this.$router.push('/questions/list')
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 接收图片路径
    getUrl (url, index) {
      // this.$on('get')
      console.log(url, 9999)
      console.log(index, 8888)
      this.options[index].img = url
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.box-card {
  margin: 15px 10px;
  .el-select {
    width: 400px;
  }
  .el-form-item {
    margin-bottom: 20px;
  }
  ::v-deep.el-textarea {
    height: 150px;
    .el-textarea__inner {
      height: 150px;
    }
  }
}
</style>
